/* styles/Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5%;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    cursor: pointer;
  }
  
  .navbar-logo a {
    color: #FFD700;
    font-size: 1.5rem;
    font-weight: 700;
    text-decoration: none;
  }
  
  .navbar-links {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
  }
  
  .navbar-links a {
    color: #777;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s;
  }
  
  .navbar-links a:hover,
  .navbar-links a.active {
    color: #FFD700;
  }
  
  @media (max-width: 992px) {
    .navbar {
      flex-direction: column;
      padding: 1rem;
    }
    
    .navbar-logo {
      /* margin-bottom: 1rem; */
      display: none;
    }
    
    .navbar-links {
      flex-wrap: wrap;
      justify-content: center;
      grid-gap: 1rem;
      gap: 1rem;
    }
    .navbar-links a{
      font-size: xx-small;
    }
  }
/* styles/Hero.css */
.hero-section {
    background-image: linear-gradient(rgba(0, 0, 51, 0.596), rgba(0, 0, 51, 0.605)),url('/PortfolioHomeImage.jpg');
    background-size: cover;
    background-position: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
  }
  
  .hero-content h1 {
    font-size: 4rem;
    font-weight: 700;
    margin-bottom: 1rem;
    letter-spacing: 2px;
  }
  
  .accent-dot {
    color: #fece2f;
  }
  
  .hero-content p {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    letter-spacing: 1px;
  }
  
  .download-btn {
    display: inline-block;
    background-color: #fece2f;
    color: #FFFFFF;
    padding: 0.8rem 2rem;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 500;
    transition: background-color 0.3s;
  }
  
  .download-btn:hover {
    background-color: #FFD700;
  }


  /* ========== RESPONSIVE ========== */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2.5rem;
    letter-spacing: 1px;
  }

  .hero-content p {
    font-size: 1.2rem;
  }

  .download-btn {
    padding: 0.6rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .download-btn {
    padding: 0.5rem 1.2rem;
    font-size: 0.95rem;
  }
}
/* styles/AboutMe.css */
.about-section {
    padding: 5rem 10%;
  }
  
  .about-content {
    display: flex;
    grid-gap: 3rem;
    gap: 3rem;
    align-items: center;
  }
  
  .about-text {
    flex: 1 1;
  }
  
  .about-text p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
  
  .contact-info {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 2rem;
    gap: 2rem;
  }
  
  .info-item h3 {
    color: #666;
    margin-bottom: 0.5rem;
  }
  
  .about-image-container {
    flex: 1 1;
  }
  .about-image {
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .about-image img {
    width: 100%;
    display: block;
  }
  
  .social-links {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 1rem;
    background-color: #1e3a8a;
    padding: 1rem;
    border-radius: 0 0 10px 10px;
    margin-top: -5px;
  }
  
  .social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
    color: #1e3a8a;
    font-size: 1.2rem;
    transition: all 0.3s;
  }
  
  .social-link:hover {
    background-color: #c32ffe;
    color: white;
  }
  
  @media (max-width: 768px) {
    .about-content {
      flex-direction: column;
    }
  
    .about-section {
      padding: 3rem 5%;
    }
  
    .about-text p {
      font-size: 1rem;
    }
  
    .social-links {
      flex-wrap: wrap;
    }
  }
  
  @media (max-width: 480px) {
    .about-section {
      padding: 2rem 3%;
    }
  
    .about-text p {
      font-size: 0.95rem;
      text-align: justify;
    }
  
    .info-item h3 {
      font-size: 1rem;
    }
  
    .social-link {
      width: 35px;
      height: 35px;
      font-size: 1rem;
    }
  }
/* styles/Education.css */
.education-section {
    padding: 5rem 10%;
    background-color: #f9f9f9;
  }
  
  .education-timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .timeline-item {
    margin-bottom: 3rem;
    display: flexbox;
  }
  
  .timeline-content {
    padding: 1.5rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    position: relative;
    width: 50%;
  }
  
  .timeline-content.left {
    margin-right: auto;
  }
  
  .timeline-content.right {
    margin-left: auto;
  }
  
  .timeline-marker {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #FFD700;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .timeline-content.left .timeline-marker {
    right: -7.5px;
  }
  
  .timeline-content.right .timeline-marker {
    left: -7.5px;
  }
  
  .year {
    color: #FFD700;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
  
  .timeline-content h3 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }
  
  .timeline-content h4 {
    color: #666;
    margin-bottom: 0.5rem;
  }
  @media (max-width: 768px) {
    .timeline-item {
      flex-direction: column;
    }
  
    .timeline-content {
      width: 100%;
      margin: 0 auto 2rem;
    }
  
    .timeline-content.left,
    .timeline-content.right {
      margin: 0 auto 2rem;
    }
  
    .timeline-marker {
      top: -10px;
      left: 50% !important;
      transform: translateX(-50%);
    }
  }
  
  @media (max-width: 480px) {
    .education-section {
      padding: 2.5rem 4%;
    }
  
    .timeline-content {
      padding: 1rem;
    }
  
    .timeline-content h3 {
      font-size: 1rem;
    }
  
    .timeline-content h4 {
      font-size: 0.95rem;
    }
  
    .year {
      font-size: 0.95rem;
    }
  }
/* styles/SkillsCards.css */
.skills-section {
  padding: 4rem 1rem;
  background-color: #f1e3fa;
  text-align: center;
}

.skills-title .section-subtitle {
  font-size: 1rem;
  color: #a855f7;
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.skills-title .section-heading {
  font-size: 2rem;
  font-weight: bold;
  color: #2c003e;
  margin-bottom: 2rem;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.skill-card {
  width: 300px;
  background-color: #fff;
  border: 2px solid #2c003e;
  border-radius: 12px;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.15);
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
  color: #2c003e;
}

.skill-card:hover {
  transform: translateY(-8px);
  box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.3);
}

.card-icon {
  font-size: 36px;
  color: #6a0dad;
  margin-bottom: 1rem;
}

.skill-card h3 {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.card-text {
  font-size: 0.95rem;
  color: #555;
  line-height: 1.4;
}

