/* src/styles/Experience.css */

.experience-section {
  padding: 1rem 1rem;
  text-align: center;
  background-color: #f9f9f9;
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #333;
}

.experience-cards {
  display: flex;
  justify-content: center;
  align-items: stretch;
  grid-gap: 2rem;
  gap: 2rem;
  flex-wrap: wrap;
  position: relative;
}

.card {
  background: #ffffff;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
  width: 100%;
  transition: all 0.5s ease;
}

.experience-card {
  flex: 1 1 100%;
  max-width: 100%;
}

.project-card {
  display: none;
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  z-index: 1;
}

.project-card.active {
  display: block;
  opacity: 1;
  position: relative;
  z-index: 2;
}

.carousel-buttons {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.carousel-btn {
  padding: 0.6rem 1.5rem;
  font-size: 1rem;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.carousel-btn:hover {
  background-color: #0056b3;
}

/* Responsive */
@media (max-width: 768px) {
  .experience-cards {
    flex-direction: column;
    align-items: center;
  }

  .card {
    width: 90%;
  }

  .project-card {
    position: relative;
  }

  .experience-card {
    margin-bottom: 1.5rem;
  }

  .carousel-btn {
    padding: 0.5rem 1rem;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem;
  }

  .carousel-btn {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
  }
}

/* styles/ScrollToTop.css */
.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background-color: #c32ffe;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.scroll-to-top.visible {
  opacity: 1;
  visibility: visible;
}

.scroll-to-top:hover {
  background-color: #a415e0;
  transform: scale(1.1);
}

/* Mobile optimization */
@media (max-width: 480px) {
  .scroll-to-top {
    bottom: 16px;
    right: 16px;
    width: 38px;
    height: 38px;
    font-size: 0.9rem;
  }
}


